<template>
  <v-container id="regular-tables" fluid tag="section">
    <base-material-card
      icon="mdi-clipboard-text"
      title="لائحة النقص"
      class="px-5 py-3"
    >
      <template slot="after-heading">
        لائحة النقص
      </template>
      <div class="d-flex flex-row justify-end">
        <v-btn
          @click="addShotrtage"
          color="primary"
          class="mb-2 align-self-end "
          dark
          >أضافة نقص
        </v-btn>
      </div>
      <v-data-table
        :loading="loading"
        :headers="headers"
        :items="shortages"
        :items-per-page="10"
        class="elevation-1"
      >
        <template v-slot:item.actions="{ item }">
          <v-icon
            color="red accent-2"
            class="mr-2"
            small
            @click="deleteItem(item)"
          >
            mdi-delete
          </v-icon>
          <v-icon color="blue darken-4" small @click="show(item)">
            mdi-eye
          </v-icon>
        </template>
      </v-data-table>
    </base-material-card>
    <v-dialog v-model="showDialog" width="500">
      <v-card>
        <v-card-title
          class="headline pb-2 text-right blue darken-4 white--text"
        >
          عرض النقص
        </v-card-title>

        <v-card-text class="pt-3 text-right" style="direction: rtl">
          <b>اسم المدرسة: </b> {{ shownItem.school_name }}
          <br />
          <b> الصف: </b> {{ shownItem.grade_name }}
          <br />
          <b> المادة: </b>{{ shownItem.material_name }}
          <br />
          <b> الساعات :</b> {{ shownItem.houres }}
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer></v-spacer>
          <v-btn color="blue darken-4" text @click="showDialog = false">
            حسنا
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <div class="py-3" />
  </v-container>
</template>
<script>
import http from "@/repo/teachAndLearnHttp";
export default {
  mounted() {
    this.fetchShortages();
  },
  data() {
    return {
      shortages: [],
      headers: [
        {
          text: "school name",
          align: "start",
          sortable: false,
          value: "school_name"
        },
        { text: "grade name", value: "grade_name" },
        { text: "material name", value: "material_name" },
        { text: "Actions", value: "actions" }
      ],
      loading: false,
      showDialog: false,
      shownItem: {}
    };
  },
  methods: {
    addShotrtage() {
      this.$router.push("/add-shortage");
    },
    fetchShortages() {
      this.loading = true;
      http()
        .get("shortage")
        .then(({ data }) => {
          this.shortages = data.shortages;
          this.loading = false;
        });
    },
    deleteItem(item) {
      this.shortages = this.shortages.filter(e => e.id != item.id);
    },
    show(item) {
      this.shownItem = item;
      this.showDialog = true;
    }
  }
};
</script>
